.Info{
    margin-bottom: 150px;
    margin-left: 123px;
    width: 666px;
    display: flex;
    margin-top: 190px;
    flex-direction: column;
}
.Text{
    display: flex;
    margin-bottom: 26px;
}

.Name p{
    font-family: 'Playfair Display', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 96px;
    line-height: 45px;
    color: #425A20;
    margin-bottom: 47px;
}

.Text div{
    width: 285px;
}

.Text div:nth-child(1){
    margin-right: 58px;
}

.Text div p:nth-child(1){
    font-family: 'FuturaDemiC', serif;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 21px;
    color: #425A20;
    margin-top: 0;
}

.Text div p:nth-child(2){
    font-family: 'FuturaBookC', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: rgba(68, 68, 68, 0.9);
}

.Buttons button{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 46px;
    background: #597D4E;
    border-radius: 5px;
    border: none;
    font-family: 'FuturaDemiC', serif;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    color: white;
}

.Buttons{
    align-items: center;
    display: flex;
}

.MainButtonContainer{
    position: absolute;
    width: 200px;
    height: 46px;
    border: 0.5px solid #000000;
    box-sizing: border-box;
    border-radius: 5px;
    transform: rotate(-2.09deg);
}

.view{
    align-items: center;
    display: flex;
    margin-left: 40px;
}

.view img{
    margin-right: 7px;
    width: 22px;
    height: 22px;
}

.view p{
    font-family: 'FuturaDemiC', serif;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 21px;
    color: #425A20;
}

.border{
    position: absolute;
}