.Footer{
    margin-left: 120px;
    display: flex;
}


.FooterMenu{
    justify-content: center;
    max-width: 1110px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
}

.Slyder{
    align-items: center;
    margin-left: 140px;
    width: 400px;
    display: flex;
}

.Slyder p{
    margin: 0;
}

.Slyder div:nth-child(1){
    margin-right: 20px;
}

.Slyder div:nth-child(2){
    margin-right: 13px;
}

.Slyder div:nth-child(4){
    margin-left: 13px;
}

.Slyder div:nth-child(5){
    margin-left: 20px;
}

.Slyder img{
    cursor: pointer;
}

.Slyder div:nth-child(3) hr{
    margin: 0;
}

.Slyder div:nth-child(3) hr{
    border: 1px solid rgba(66, 90, 32, 0.38);
    width: 210px;
    transform: rotate(180deg);
}

.Slyder div:nth-child(5) img{
    transform: rotate(-180deg);
}

@media screen and (max-width: 1041px) {
    .Slyder div:nth-child(3) hr:nth-child(2){
        bottom: -58px;
    }
}