.Menu{
    margin-top: 150px;
    display: flex;
    flex-direction: column;
}

.Menu p{
    width: 400px;
    margin: 0 auto 39px;
    font-family: 'Playfair Display', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 48px;
    line-height: 64px;
    text-align: center;
    cursor: pointer;
}

.Menu p:nth-child(5){
    margin-bottom: 128px;
}

.MenuContainer{
    display: flex;
    justify-content: center;
}

.LeftPicture{
    position: fixed;
    margin-right: 600px;
    display: none;
    margin-top: 230px;
}

.RightPicture{
    display: none;
    position: fixed;
    margin-left: 600px;
    margin-top: 200px;
}