.Nav a{
    font-family: 'FuturaDemiC', serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: black;
    text-decoration: none;
}

.Nav{
    justify-content: space-between;
    display: flex;
    align-items: center;
}

.AA{
    justify-content: space-evenly;
    display: flex;
    width: 522px;
}

.Search{
    border-left: 1px solid #000000;
    border-right: 1px solid #000000;
    justify-content: center;
    display: flex;
    align-items: center;
    width: 60px;
}
.Menu{
    display: flex;
    justify-content: flex-end;
    width: 66px;
}

.Nav img{
    cursor: pointer;
}

.Close{
    display: flex;
}

.Close p{
    font-family: 'FuturaDemiC', serif;
    align-items: center;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    margin-top: 0;
    margin-bottom: 0;
    margin-right: 14px;
}

.Close img{
    cursor: pointer;
}